@value todoWhite from '../../common/colors.css';
@value todoBlack from '../../common/colors.css';
@value todoGray from '../../common/colors.css';

.header {
  width: 100%;
  text-align: center;
  background-color: todoGray;
  position: relative;
}

.title {
  font-size: 2.5rem;
  margin: 0;
  padding: 0.5em;
  color: todoWhite;
}

.logout {
  font-size: 1rem;
  position: absolute;
  top: 2rem;
  right: 0.5rem;
  padding: 0.8em;
  background-color: todoBlack;
  color: todoWhite;
  outline: 0;
  border: 0;
  border-radius: 10%;
  cursor: pointer;
}

@media screen and (max-width: 1045px) {
  .logout {
    top: 2rem;
    right: 1rem;
    padding: 0.6em;
  }
}
